$bg: #FFFFFF;
.js-loader-color-theme,
.btn-picto-color {
    position: fixed;
    top: auto;
    left: auto;
    bottom: -100px;
    right: 1.5rem;
    border-radius: 50%;
    z-index: 999;
    padding: 0;
    opacity: 0;
    background-color: transparent;
    border: 0;
    transition: transform 0.321s linear, bottom 0.321s ease-in-out, opacity 0.321s linear;

    &.active {
        transform: rotate(90deg);
    }

    &.show {
        opacity: 1;
        bottom: 1.5rem;
    }

    img {
        border-radius: 50%;
        object-fit: cover;
        object-position: center;
        width: 65px;
        height: 65px;
    }
}

.js-loader-color-theme {
    opacity: 1;
    bottom: 1.4rem;

    span {
        width: 67px;
        height: 67px;
        font-size: 1.5rem;
    }

    &.hide {
        opacity: 0;
    }
}


#change--color {
    position: fixed;
    top: auto;
    left: auto;
    bottom: 3rem;
    right: 3rem;
    background-color: $bg;
    z-index: 99;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
    justify-content: space-between;
    border-radius: 20px;
    color: black;
    transform: translateY(calc(100% - -3rem));
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    height: 0;
    transition: height 0.321s ease-in-out, transform 0.321s ease-in-out;

    a {
        color: black;
    }

    &.active {
        transform: translateY(0);
        height: 685px;
    }

    #reset,
    #close {
        position: absolute;
        top: 0.5rem;
        left: auto;
        right: 1rem;
        font-size: 1.2rem;
        border: none;
        background: transparent;
        color: black;
        z-index: 100;
    }

    #reset {
        right: 3rem;
    }

    #setting-alert {
        font-size: 1.2rem;
        border: none;
        background: transparent;
    }

    .box--alert {
        position: absolute;
        top: 0;
        left: auto;
        right: 0;
        width: 200px;
        opacity: 0;
        transition: width 0.5s ease-in-out, right 0.321s ease-in-out, opacity 0.100s ease-in-out;
        z-index: -1;

        &.active {
            opacity: 1;
            width: 500px;
            right: calc(100% + 2rem);
            transition: width 0.050s ease-in-out, right 0.321s ease-in-out, opacity 0.100s ease-in-out;
        }
    }

    .app-section--is-hidden {
        display: none;
    }

    svg,
    .icon {
        width: 10px;
        height: 10px;
        display: inline-block;
    }

    ul {
        list-style: none;
        margin: 0;
    }


    .app-sidebar {
        flex: 1 1 100%;
        width: 100%;
        font-weight: bold;
    }

    .app-color {
        flex: 1 1 100%;
        width: 100%;
        min-height: 200px;
        font-weight: bold;
    }

    .app-editor-section {
        padding: 1rem 1rem 0 1rem;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;

    }

    .app-editor-section__body {
        background-color: darken($bg, 10);
        padding: 0.5rem;
        border-radius: 4px;
    }

    .app-color-picker {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        .app-color-picker__preview {
            box-shadow: 0 5px 10px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        }

        .app-color-picker__native {
            opacity: 0;
            width: 30px;
        }

        input {
            background-color: white;
            border: 0;
            height: 30px;
        }

        .app-editor__input-group {
            margin-left: 1rem;
        }

        .app-form-control {
            max-width: 160px;
            padding-left: 3rem;
        }
    }


    .app-content__group {
        margin: 0;
        padding: 1rem;
        background-color: $bg;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;

        > li {
            background-color: $bg;
            padding: 0.5rem;
            border-radius: 4px;
            margin-bottom: 1rem;

            &:last-child {
                &.app-content-item--is-selected {
                    background-color: darken($bg, 10);
                    border-radius: 8px;
                }

                &:focus,
                &:hover {
                    background-color: darken($bg, 15);
                    cursor: pointer;
                }
            }
        }
    }

    .app-content-item--is-selected {
        background-color: darken($bg, 10);
        border-radius: 8px;
    }

    .js-app-content__feedback-colors,
    .js-app-content__main-colors {
        padding-left: 0;
        text-align: center;


        .app-content-item__text {
            padding: 0.2rem;
            width: 100%;
            display: inline-block;
            border-radius: 8px;

            &:focus,
            &:hover {
                background-color: darken($bg, 15);
                cursor: pointer;
            }
        }
    }

    .btn--subtle {
        position: relative;
        z-index: 10;
        flex: 1 1 100%;
        width: 100%;
        margin: 0 auto 1rem auto;
        max-width: 90%;
        border-color: darken($bg, 60);
        background-color: darken($bg, 90);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        transition: transform 0.1s ease-in, background-color 0.1s linear;

        &:hover {
            transform: scale(1.05);
            background-color: darken($bg, 80);
        }

        &:focus,
        &:active {
            background-color: darken($bg, 70);
        }
    }

    .text--bottom {
        position: relative;
        top: auto;
        bottom: 11px;
        left: 0;
        padding: 0 2.5rem 0 1rem;
        font-size: 11px;
        font-weight: 600;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        flex: 1 1 100%;
        width: 100%;
    }
}

